import React, { useState, useEffect } from 'react';

// components
import Header from '../header';
import Content from '../content';
import Footer from '../footer';
import Nav from '../nav';

// style
import '../../assets/scss/style.scss';

function App() {
  const [enabled, setEnabled] = useState(window.innerWidth >= 960);
  const [size, setSize] = useState([window.innerWidth, window.innerHeight]);

  function callback() {
    if(size[0] < 960)
      setEnabled(!enabled);
  }

  useEffect(() => {
    if (enabled && size[0] < 960)
       setEnabled(false);
    else if (!enabled && size[0] >= 960)
       setEnabled(true);
  }, [size]);

  useEffect(() => {
    function updateSize() {
      setSize([window.innerWidth, window.innerHeight]);
    }

    window.addEventListener('resize', updateSize);

    return () => window.removeEventListener('resize', updateSize);
  }, []);


  return (
    <>
      <Header callbackMaster={callback} />
      <Content />
      <Footer />
      <Nav callbackMaster={callback} enabled={enabled} />
    </>
  );
}

export default App;