import React from 'react';

class Button extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            counter: 1
        };

        this.title = this.props.name || 'Botão';

        // Aqui utilizamos o `bind` para que o `this` funcione dentro da nossa callback
        this.receiveClick = this.receiveClick.bind(this);
    }

    receiveClick() {
        this.setState({
            counter: this.state.counter + 1
        });

        typeof this.props.callback === 'function' && this.props.callback(`Button have received ${this.state.counter} clicks`);
    }

    render() {
        return (
            <>
                <button type="button" onClick={this.receiveClick}>{this.title} - {this.props.dataFromParent}</button>
            </>
        )
    }
}

export default Button;