import React from 'react';

import Button from '../button';

export default class Header extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            data: 'Hello World',
            message: ''
        }

        this.myCallback = this.myCallback.bind(this);
    }

    myCallback(info) {
        this.setState({
            message: info
        });

        this.props.callbackMaster();
    }

    render(){
        return (
            <>
                <header>
                    {this.state.message || 'Button have received 0 clicks'}
                    <Button name="menu" callback={this.myCallback} dataFromParent={this.state.data} />
                </header>
            </>
        )
    }
}