import React from 'react';

import Button from '../button';

export default class Nav extends React.Component {
    constructor(props){
        super(props);

        this.myCallback = this.myCallback.bind(this);
    }

    myCallback() {
        this.props.callbackMaster();
    }

    render() {
        return (
            <>
                <nav hidden={!this.props.enabled}>
                    Nav - {this.props.enabled.toString()}
                    <Button name="fechar" callback={this.myCallback} />
                </nav>
            </>
        )
    }
}